.calculation {
  min-height: 100vh;
  background: $background-pattern;
  box-shadow: 0px -0.11px 16.9495px rgba($dark-blue-rgb, .33); }

.calculation--opened {
  box-shadow: none;
  background: rgba($black-rgb, .3); }

.calculation__wrapper {
  position: relative;
  display: flex;
  min-width: 1440px;
  min-height: inherit;

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    min-width: 768px; }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    min-width: 320px; } }

.calculation__show-popup {
  margin: auto;
  padding: 16px 32px;
  background: none;
  border: 1px solid $white;
  border-radius: 6px;
  filter: drop-shadow(0px 0px 44px $grey);
  cursor: pointer;
  font-family: inherit;

  &:hover,
  &:active {
    background-color: $white;
    color: $black;
    filter: unset;
    cursor: url('../../img/icons/pointer.png'), pointer;

    &:disabled {
      background-color: $light-grey;
      color: $white;
      border: none;
      filter: unset;
      cursor: default; } }

  @include fstyle(16px, 24px, $white, 500);

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    padding: 12px 24px;
    font-size: 12px;
    line-height: 16px; } }
