.popup {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 417px;
  width: 493px;
  margin: auto;
  padding: 27px 27px 32px 32px;
  background-color: $white;
  border-radius: 30px;

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    width: 398px;
    margin-top: 120px; }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    min-height: 536px;
    width: 288px;
    margin: 0 auto;
    padding: 16px;
    border-radius: 0; } }

.popup__button {
  padding: 0;
  border: none;
  background: none;
  font-family: inherit;
  cursor: pointer; }

.popup__text {
  margin: 0; }

.popup__header {
  margin-bottom: 16px;

  @include fstyle(28px, 40px, $black, 500);

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    width: 235px; }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    width: 151px;
    margin-top: 16px;
    font-size: 18px;
    line-height: 24px; } }

.popup__annotation {
  width: 488px;
  margin-bottom: 24px;

  @include fstyle(14px, 24px, $dark-grey);

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    width: 369px; }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    width: 288px;
    font-size: 12px;
    line-height: 16px; } }

.popup__label,
.popup__discont-text,
.popup__text--repayment {
  @include fstyle(14px, 24px, $black, 500); }

.popup__label--discont {
  margin-right: 16px;
  padding: 6px 12px;
  background: $dark-shade-grey;
  border-radius: 50px;
  font-weight: 400;
  cursor: pointer;

  &:hover {
    cursor: url('../../img/icons/pointer.png'), pointer;
    background: $darker-grey;
    color: $black; }

  &:active {
    cursor: url('../../img/icons/pointer.png'), pointer;
    background: $background-pattern;
    color: $white; }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    margin-right: 0; } }

.popup__label--selected {
  margin-right: 16px;
  padding: 6px 12px;
  color: $white;
  background: $background-pattern;

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    margin-right: 0; } }

.popup__salary-value {
  min-height: 22px;
  width: 474px;
  margin-top: 8px;
  margin-bottom: 8px;
  padding-top: 8px;
  padding-left: 10px;
  padding-bottom: 8px;
  border: 1px solid $darker-grey;
  border-radius: 3px;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button, {
    -moz-appearance: none;
    -webkit-appearance: none;
    margin: 0; }

  &:hover {
    border-color: $black; }

  &:focus {
    border-color: $darker-grey; }

  &:disabled {
    border-color: $dark-grey; }

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    width: 389px; }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    width: 274px; } }

.popup__form {
  @media (min-width: $mobile-width) and (max-width: $tablet-width-only) {
    display: flex;
    flex-direction: column; } }

.popup__salary-value--attention {
  margin-bottom: 4px;
  border-color: $bright-red; }

.popup__calculate {
  margin-bottom: 24px;

  @include fstyle(14px, 24px, $bright-red, 500);

  &:hover {
    color: $orange;
    cursor: url('../../img/icons/pointer.png'), pointer; }

  &:active {
    color: $bright-red; }

  @media (min-width: $mobile-width) and (max-width: $tablet-width-only) {
    width: 76px; } }

.popup__calculate--enabled {
  margin-bottom: 16px; }

.popup__discont-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 40px;

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    display: grid;
    grid-template-columns: 73px 57px;
    grid-template-rows: 24px 36px;
    gap: 24px 8px;
    width: 144px;
    margin-bottom: 140px; } }

.popup__discont-text {
  margin-right: 32px;

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    grid-column: 1 / -1;
    margin-right: 0; } }

.popup__close-button {
  position: relative;
  height: 40px;
  width: 40px;

  @include absoluteElementTopRight(16px, 16px);

  &::before,
  &::after {
    content: '';
    height: 4px;
    width: 24px;
    margin-top: -2px;
    margin-left: -12px;
    background-color: $bright-red;
    border-radius: 10px;

    @include absoluteElement(absolute, 50%, 50%); }

  &::before {
    transform: rotate(45deg); }

  &::after {
    transform: rotate(-45deg); }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    height: 24px;
    width: 24px;

    &::before,
    &::after {
      top: 16px;
      right: 16px; } } }

.popup__submit {
  min-height: 56px;
  width: 488px;
  padding-top: 16px;
  padding-bottom: 16px;
  border-radius: 6px;
  box-shadow: 0px 0px 24px rgba($deep-red, .33);
  background: $background-pattern;

  @include fstyle(16px, 24px, $white, 500);

  &:disabled {
    background: $light-grey;
    box-shadow: none; }

  &:hover {
    cursor: url('../../img/icons/pointer.png'), pointer;
    background: $bright-red; }

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    width: 389px; }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    min-height: 16px;
    width: 288px;
    padding-top: 12px;
    padding-bottom: 12px;
    font-size: 12px;
    line-height: 16px; } }

.popup__repayment-wrapper {
  margin-bottom: 20px; }

.popup__text--repayment {
  margin-bottom: 16px;

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    width: 151px; } }

.popup__repayments-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.popup__repayment-year {
  color: $dark-grey; }

.popup__text--amount {
  @include fstyle(14px, 24px, $black); }

.popup__repayment-item {
  position: relative;
  min-height: 24px;
  width: 457px;
  padding-top: 16px;
  padding-left: 31px;
  padding-bottom: 16px;

  &:first-of-type {
    padding-top: 0; }

  &::before {
    content: '';
    height: 1px;
    width: 488px;
    background-color: $darker-grey;

    @include absoluteElementBottomLeft(0, 0); }

  @media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
    width: 358px;

    &::before {
      width: 389px; } }

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    width: 257px;

    &::before {
      width: 288px; } } }

.popup__repayment-checkbox + .popup__repayment-label {
  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    height: 18px;
    width: 18px;
    border: 1px solid $darker-grey;
    border-radius: 6px;

    @include absoluteElement(absolute, 2px, -31px); }

  &:active {
    cursor: url('../../img/icons/pointer.png'), pointer; }

  &:hover {
    cursor: url('../../img/icons/pointer.png'), pointer;

    &::before {
      border: 1px solid $black; } } }

.popup__repayment-checkbox:checked + .popup__repayment-label {
  &::before {
    height: 20px;
    width: 20px;
    border: none;
    background: $background-pattern;
    background-image: url("data:image/svg+xml,%3Csvg width='14' height='11' viewBox='0 0 14 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.45455 8.70149L1.11364 5.25373L0 6.40299L4.45455 11L14 1.14925L12.8864 0L4.45455 8.70149Z' fill='white'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: 50% 50%; } }

.popup--calculation-enabled {
  margin-top: 120px;
  margin-bottom: 88px;

  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    margin: 0 auto; } }

.popup__discont-wrapper--enabled {
  @media (min-width: $mobile-width) and (max-width: $mobile-width-only) {
    margin-bottom: 40px; } }

.popup__repayment-checkbox:disabled + .popup__repayment-label {
  &::before {
    height: 20px;
    width: 20px;
    border: none;
    background: $light-grey;
    background-image: url("data:image/svg+xml,%3Csvg width='14' height='11' viewBox='0 0 14 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.45455 8.70149L1.11364 5.25373L0 6.40299L4.45455 11L14 1.14925L12.8864 0L4.45455 8.70149Z' fill='white'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: 50% 50%; }

  &:hover,
  &:active {
    cursor: default; } }

.popup__user-attention {
  margin: 0;

  @include fstyle(10px, 12px, $bright-red); }

.popup__message-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 250px;
  margin-top: -25px;
  margin-left: -125px;
  background-color: $white;
  border: 1px solid $black;
  border-radius: 10px;

  @include absoluteElement(absolute, 50%, 50%); }
